import {useEffect} from "react";
import {useRequest} from 'ahooks';
import {getTokens, getPairs} from "../../service";
import {useDispatch} from "react-redux";
import {setTokenList, setPairs} from "./actions";

export default function ApplicationUpdater(): null {
  const { data: tokensData } = useRequest(getTokens, {defaultParams: [1]})
  const { data: pairsData, run: pairsRun } = useRequest(getPairs, {manual: true})
  const dispatch = useDispatch()
  useEffect(() => {
    // @ts-ignore
    if (tokensData?.success && tokensData.data) {
      let data = tokensData?.data as Currency[]
      dispatch(setTokenList(data))
      let token = data[0].tokens[0]
      if (token?.address) {
        pairsRun(token.address, token.cid)
      }
    }
  }, [tokensData, dispatch, pairsRun])

  useEffect(() => {
    // @ts-ignore
    if (pairsData?.success && pairsData.data) {
      dispatch(setPairs(pairsData?.data as Pairs[]))
    }
  }, [pairsData, dispatch])

  // useEffect(() => {
  //
  // }, [tokensData, dispatch])

  return null
}
