import React, { useMemo, useEffect, useState } from 'react'
import './index.scss'
import { Icon } from '../../../../components'
import cn from 'classnames'
import SimpleBar from 'simplebar-react'
import { UnitToken } from '../../../../state/application/reducer'
import { ListType, SwapLiquidityItem, } from '../../../../state/list/reducer'
import { useView } from '../../../../state/view/hooks'
import { useFavtorite } from '../../../../state/list/hooks'

export type ListProps = {
    data: ListType,
    unitToken: UnitToken,
    setLiquidity: (item: SwapLiquidityItem) => void
}

type SimpleBarRowProps = {
    isETH: boolean,
    index: number,
    symbol: 'ETH' | "$",
    data: SwapLiquidityItem,
    setSaved: (n: number) => void
    setLiquidity: (item: SwapLiquidityItem) => void,
}

const SimpleBarRow: React.FC<SimpleBarRowProps> = ({ data, isETH, symbol, index, setSaved, setLiquidity }) => {
    const { saved, token, trade24h, liquidityAmount, price } = data
    return (
        <div className='swapbody__item' >
            <button >
                <Icon symbol={saved ? 'favoriteActive' : 'favorite'} size={18} />
            </button>
            <a onClick={(event) => {
                event.preventDefault();
                setLiquidity(data)
            }}>
                <div className='name'>
                    <div className='pair'>
                        <button><Icon symbol='eth' /></button>
                        <div>
                            <span>{token.from}</span>
                            <span className='color2'>{token.to}</span>
                        </div>
                    </div>
                </div>
                <div className={cn('volume', { center: !isETH })}>
                    <span>
                        {!isETH && <i className='color3'>$</i>}
                        {trade24h}
                    </span>
                    {isETH && <span className='color3'>{symbol}</span>}
                </div>
                <div className={cn('liquidity', { center: !isETH })}>
                    <span>
                        {!isETH && <i className='color3'>$</i>}
                        {liquidityAmount}
                    </span>
                    {isETH && <span className='color3'>ETH</span>}
                </div>
                <div className='price'>
                    <span>
                        {!isETH && <i className='color3'>$</i>}
                        {price.value}
                    </span>
                    <span className='color3 dy'>
                        {isETH && <span className='color3'>ETH</span>}
                        <Icon symbol={price.float} size={14} />
                        <i className={cn({ [price.float]: 1 })}>{price.parcent}</i>
                    </span>
                </div>
            </a>
        </div>
    )
}


const List: React.FC<ListProps> = ({ data, unitToken, setLiquidity }) => {
    const { isMobile } = useView()
    const setSaved = useFavtorite()

    const isETH = unitToken.token === "ETH"
    const LiquidityElements = useMemo(() => {
        return data.map((item, i) => {
            return (
                <SimpleBarRow
                    key={i.toString()}
                    index={i}
                    isETH={isETH}
                    symbol={unitToken.symbol}
                    data={item}
                    setSaved={setSaved}
                    setLiquidity={setLiquidity} />
            )
        })
    }, [data, unitToken, setLiquidity])

    if (!data || data.length === 0) {
        return null
    }
    // console.log('data', data)
    return (
        <div className='swap-list'>
            <div className={cn('swap-header', { isMobile: isMobile })}>
                <div>Name</div>
                <div>Volume,24h</div>
                <div>Liquidity</div>
                <div>Price</div>
            </div>
            {
                isMobile ? <div className='m-swap-body'>
                    {LiquidityElements}
                </div> : <SimpleBar style={{ height: '400px' }} forceVisible="y">
                    {LiquidityElements}
                </SimpleBar>
            }
        </div>
    )
}

export default List
