import React, { useEffect, useRef, useState } from 'react'
import './index.scss'
import { init, dispose } from 'klinecharts'
import generatedKLineDataList from './generatedKLineDataList'
import {ResizableElement} from '../'
import TopBar from "./TopBar";
import SideBar from "./SideBar";
import {useView} from "../../state/view/hooks";
import {useApplication} from "../../state/application/hooks";
import {useRequest} from "ahooks";
import {getKlineData} from "../../service";
// import BottomBar from "./BottomBar";

export enum Periods {"1m" = "1m", "5m" = "5m", "30m" = "30m", "1h" = "1h", "6h" = "6h", "12h" = "12h", "1d" = "1d"}

function enumValues<T extends object>(enumeration: T): Array<T[keyof T]> {
  return Object
    .keys(enumeration)
    .filter(k => isNaN(Number(k)))
    .map(k => enumeration[k as keyof T]);
}

export const periods = enumValues(Periods)

const Kline:React.FC = () => {
  const [period, setPeriod] = useState(Periods['1m'])
  const {pairs} = useApplication();
  const { data: klineData, run: klineRun, loading: klineLoading } = useRequest(getKlineData, {manual: true})

  // console.log('klineData---')
  // console.log(klineData)
  const {isMobile} = useView()
  const [isFullscreen, setIsFullscreen] = useState(false)
  const box = useRef<HTMLElement | null>(null)
  const boxHeight = useRef(0)
  const kLineChart = useRef<any>(null)
  const lastLoad = useRef<{
    pair: Pairs | null
    period: Periods | null
  }>({pair: null, period: null})

  useEffect(() => {
    // console.log('pairs---')
    // console.log(pairs)
    let pair = pairs[0]
    if (pair) {
      lastLoad.current = {pair, period}
      klineRun(pair.address, period)
    }
  }, [klineRun, pairs, period])

  useEffect(() => {
    if (klineData) {
      const _data = klineData.data as [string, string, string, string, string, string, number][];
      const decimal = lastLoad.current.pair?.token0.decimal as number
      const fixed = Math.pow(10, decimal)
      let data = _data.map(([open, close, high, low, volume, turnover, timestamp]) => {
        return {
          open: Number(open) / fixed,
          close: Number(close) / fixed ,
          high: Number(high) / fixed,
          low: Number(low) / fixed,
          volume: Number(volume) / fixed,
          turnover: Number(turnover) / fixed,
          timestamp
        }
      })

      kLineChart.current && kLineChart.current.applyNewData(data)

      // console.log(data)
      // console.log(generatedKLineDataList())
    }
  }, [klineData])

  const resize = () => {
    // console.log('resize')
    kLineChart.current && kLineChart.current.resize()
  }

  useEffect(() => {
    box.current = document.getElementById('kline-wrapper')
    kLineChart.current = init('kline', isMobile ? {
      yAxis: {inside: true,},
      candle: {
        tooltip: {
          showType: 'rect',
          rect: {
            paddingLeft: 0,
            paddingRight: 0,
            paddingTop: 0,
            paddingBottom: 6,
            offsetLeft: 4,
            offsetTop: 4,
            borderRadius: 3,
            borderSize: 0,
            borderColor: 'rgba(255, 255, 255, 0)',
            fillColor: 'rgba(0, 0, 0, .5)',
          },
        }
      }
    } : {})
    kLineChart.current.createTechnicalIndicator('VOL', false, {height: 60})
    // kLineChart.current.setScrollEnabled(!isMobile)
    // kLineChart.current.applyNewData(generatedKLineDataList())
    window.addEventListener('resize', resize)

    return () => {
      dispose('kline')
      kLineChart.current = null
      window.removeEventListener('resize', resize)
    }
  }, [isMobile])

  const onFullScreen = () => {
    setIsFullscreen(true)
    if (box.current) {
      boxHeight.current = box.current.clientHeight
    }

    setTimeout(resize, 60)
  }

  const exitFullscreen = () => {
    setIsFullscreen(false)
    if (box.current) {
      box.current.style.height = boxHeight.current + 'px'
    }
    setTimeout(resize, 60)
  }

  return (
    <>
      <div  className={`kline-wrapper${isFullscreen ? ' fullscreen landscape' : ''}`}>
        {!isMobile && <TopBar kLineChart={kLineChart} onFullScreen={onFullScreen} period={period} setPeriod={setPeriod}/>}
        <ResizableElement className='kline-center' id='kline-wrapper' throttle={50} onResize={resize}>
          {!isMobile && <SideBar kLineChart={kLineChart}/>}
          <div id="kline" className="kline-chart"/>
        </ResizableElement>
        {/*<BottomBar />*/}
        {isFullscreen && <button onClick={exitFullscreen} className={'exitFullScreen'}>退出全屏</button>}
      </div>
    </>
  )
}
export default Kline;
